import React, {useEffect, useState} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {_getCategory} from "../../../../../redux/actions/public/category";
import {useParams} from "react-router-dom";
import {PageHeaderAlt} from "../../../../../components/layout-components/PageHeaderAlt";
import {Card, Typography, Row, Col, Descriptions} from "antd";
import moment from "moment";

const TravelCategorySingle = (props) => {
    let {_getCategory,data,loading} = props
    let {id} = useParams()

    useEffect(() => {
        _getCategory(id, 'slug')
    }, [_getCategory])

    return (
        <>
            <PageHeaderAlt background="/img/others/bg-doodle.jpg" cssClass="bg-primary" overlap>
                <div className="py-5 my-md-5 text-center">
                    <div>
                        <Typography.Title level={1} className={'text-white font-weight-bold'}>{data?.name ?? id ?? "-"}</Typography.Title>
                        <Typography.Text className={'text-white font-weight-bold'}>{data?.slug ?? "-"}</Typography.Text>
                    </div>
                </div>
            </PageHeaderAlt>
            <div className="container my-4">
                <div style={{width: "100%", maxWidth: 1080, margin: "0 auto"}}>
                    <Row gutter={24}>
                        <Col xs={{
                            order:2,
                            span:24
                        }} lg={{
                            order:1,
                            span:8
                        }}>
                            <Card bordered={false} loading={props?.loading ?? false}>
                                <Descriptions title={'Taxonomy Info'} loading={loading}>
                                    <Descriptions.Item label={'name'} span={24}>{data?.name ?? "-"}</Descriptions.Item>
                                    <Descriptions.Item label={'slug'} span={24}>{data?.slug ?? "-"}</Descriptions.Item>
                                    <Descriptions.Item label={'Is Published'} span={24}>{data?.is_published ?? "-"}</Descriptions.Item>
                                    <Descriptions.Item label={'Is Verify'} span={24}>{data?.is_verify ?? "-"}</Descriptions.Item>
                                    <Descriptions.Item label={'Created'} span={24}>{moment(data?.createdAt).format("DD, MMM YYYY HH:mm:ss a")?? "-"}</Descriptions.Item>
                                    <Descriptions.Item label={'Updated'} span={24}>{moment(data?.updatedAt).format("DD, MMM YYYY HH:mm:ss a")?? "-"}</Descriptions.Item>
                                </Descriptions>
                            </Card>
                        </Col>
                        <Col xs={{
                            order:1,
                            span:24
                        }} lg={{
                            order:2,
                            span:16
                        }}>
                            <Card bordered={false} loading={loading ?? false}>
                                <div dangerouslySetInnerHTML={{__html:props?.data?.about?.content ?? ""}}/>
                            </Card>
                        </Col>
                        <Col xs={{
                            order:3,
                            span:24
                        }} lg={{
                            order:3,
                            span:24
                        }}>
                            <Card bordered={false} loading={loading ?? false} title={'Travel Similar'}>
                                <div dangerouslySetInnerHTML={{__html:props?.data?.about?.content ?? ""}}/>
                            </Card>
                        </Col>

                    </Row>

                </div>
            </div>
        </>
    )
}

TravelCategorySingle.propTypes = {
    _getCategory: PropTypes.func.isRequired,
}
TravelCategorySingle.defaultProps = {}

export default connect(({Taxonomy}) => {
    let {loading,data,message} =Taxonomy
    return {loading,data,message}
}, {_getCategory})(TravelCategorySingle)
